@import '../../_styles/var.scss';

.container {
  flex-wrap: wrap;

  .column {
    flex-basis: 33.3%;
    flex-direction: column;
  }

  .item {
    flex-direction: column;
    padding: 0px 0px 30px 0px;
    flex-grow: 0;
    align-items: center;

    .slider {
      display: block;
      max-width: 300px;

      .sliderItem {

        .img {
          object-fit: contain;
          border-radius: 5px;
          box-shadow: 0 2px 4px -1px rgba(0, 0, 0, .2), 0 4px 5px 0 rgba(0, 0, 0, .14), 0 1px 10px 0 rgba(0, 0, 0, .12);
          margin: 13px;
          width: calc(100% - 26px);
        }

        .img150 {
          height: 150px;
        }

        .img275 {
          height: 275px;
        }

        .img488 {
          height: 488px;
        }

        .img200 {
          height: 200px;
        }

        .img300 {
          height: 300px;
        }

        .img400 {
          height: 400px;
        }

        .img500 {
          height: 500px;
        }

        .img600 {
          height: 600px;
        }

        .img700 {
          height: 700px;
        }

        .img800 {
          height: 800px;
        }
      }
    }

    .info {
      font-size: large;
      font-weight: 400;
      flex-direction: column;
      align-self: baseline;

      @include breakpoint(sm-md) {
        align-self: center;
      }

      span {
        font-size: small;
      }
    }

  }
}
