@import '../../../_styles/var.scss';

ul.list {
  font-size: 30pt;

  &>li {
    float: left;
    padding: 10px;
    list-style-type: none;

    a {
      text-decoration: none;
      color: #B6B6B6;

      &:hover {
        color: black;
        text-decoration: underline;
      }
    }
  }

  &>li.active {
    a {
      color: #4b4b4b;
    }
  }
}
